import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController } from 'ionic-angular';
import { NetFuncProvider } from "../../providers/net-func/net-func";
import { HomePage } from "../home/home";

@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {

  email: String;
  password: String;

  constructor(public navCtrl: NavController, public navParams: NavParams, private net: NetFuncProvider, private toast: ToastController) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad LoginPage');
  }

  loginBtn() {
      this.net.login(this.email, this.password).subscribe(response => {
        if(response.ok == false) {
          let toast = this.toast.create({
            duration: 3000,
            message: response.message
          });
          toast.present();
        } else {
          window.localStorage.setItem("username", response.data.username);
          this.navCtrl.push(HomePage);
        }
      });
  }

}



