import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController } from 'ionic-angular';
import { NetFuncProvider } from "../../providers/net-func/net-func";

@IonicPage()
@Component({
  selector: 'page-settings',
  templateUrl: 'settings.html',
})
export class SettingsPage {

  oldpass: String;
  newpass: String;

  constructor(public navCtrl: NavController, public navParams: NavParams, private net: NetFuncProvider, private toast: ToastController) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SettingsPage');
  }
  
  updatepassword() {
    this.net.updatepass(window.localStorage.getItem("username"), this.oldpass, this.newpass).subscribe(data => {
      if(data.ok == false) {
        let toast = this.toast.create({
          message: data.message,
          duration: 3000
        });
        toast.present();
      } else {
        let toast = this.toast.create({
          message: data.message,
          duration: 3000
        });
        toast.present();
      }
    });
  }

}



