import { Http } from '@angular/http';
import { Injectable } from '@angular/core';
import "rxjs/add/operator/map";

@Injectable()

export class NetFuncProvider {
  
  constructor(public http: Http) {
  }

  getProjects() {
    return this.http.get("http://www.graphineeastafrica.com/admin/schedules/pending").map(res => res.json());
  }

  login(email, password) {
    return this.http.post("http://www.graphineeastafrica.com/auth/login", {
      type: "technician",
      email: email,
      password: password
    }).map(res => res.json());
  }

  updatepass(username, currpass, newpass) {
    return this.http.post("http://www.graphineeastafrica.com/admin/changepasswordt", {
      username: username,
      current: currpass,
      newpassword: newpass
    }).map(res => res.json());
  }
  
  comment(username, comment, id) {
    return this.http.post("http://www.graphineeastafrica.com/admin/comment", {
      id: id,
      username: username,
      comment: comment
    }).map(res => res.json());
  }

}



