import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { NetFuncProvider } from "../../providers/net-func/net-func";

@IonicPage()

@Component({
  selector: 'page-orderdetail',
  templateUrl: 'orderdetail.html',
})
export class OrderdetailPage {
  
  id: String;
  fullname: String;
  phone: Number;
  county: String;
  town: String;
  street: String;
  category: String;
  sub: String;
  subsub: String;
  to: Date;
  from: Date;
  desc: String;
  comments: any[];

  usercomment: String;

  constructor(public navCtrl: NavController, public navParams: NavParams, private net: NetFuncProvider) {
    this.fullname = this.navParams.get("fullname");
    this.id = this.navParams.get("id");
    this.phone = this.navParams.get("phone");
    this.county = this.navParams.get("county");
    this.town = this.navParams.get("town");
    this.street = this.navParams.get("street");
    this.category = this.navParams.get("category");
    this.sub = this.navParams.get("sub");
    this.subsub = this.navParams.get("subsub");
    this.to = new Date(this.navParams.get("to"));
    this.from = new Date(this.navParams.get("to"));
    this.comments = this.navParams.get("comments");
  }

  ionViewDidLoad() {
  }

  sendComment(id) {
    this.net.comment(window.localStorage.getItem("username"), this.usercomment, id).subscribe(data => {
      this.comments = data.data.comments;
      console.log(data.data.comments);
    });
    this.usercomment = "";
  }

}



